<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>顶部批量操作按钮均为需要选中数据才能进行操作</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>角色名</label>
        <el-input v-model="queryParams.roleName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <cms-sticky>
    <section class="cms-batch">
      <cms-button v-prem="'/role/save'" icon="add" type="success" size="small" @click="$routerLink('/role/save','save')">新增</cms-button>
      <cms-button v-prem="'/role/delete'" icon="error" type="error" size="small" @click="deleteDatas(ids)">批量删除</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="roleName" label="角色名" width="110"></el-table-column>
      <el-table-column prop="remark" label="描述" min-width="220"></el-table-column>
      <el-table-column prop="createTime" label="创建时间" min-width="200">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button v-prem="'/role/update'" icon='edit' size='small' type='info' @click.stop="$routerLink('/role/update','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button v-prem="'/role/delete'" icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>

<script>
import { coreRolePage, coreRoleDelete } from '@/api/admin/role.js'
import list from '@/mixins/list'
var defaultData = {
  roleName: '',
  isSuper: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(coreRoleDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas(ids) {
      this.deleteTableDatas(coreRoleDelete, ids) // 这个才是真实的删除方法
    }
  },
  activated() {
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      this.getTableData(coreRolePage, this.queryParams)
    }
  },
  created() {
    this.getTableData(coreRolePage, this.queryParams)
  }
}
</script>

<style>
</style>



